import React, { useContext } from 'react';
import { motion } from 'framer-motion';
import { animationOne, transition } from '../animations';
import { Link } from 'react-router-dom';
import MyTitle from './MyTitle';
import { ColorContext, headerColor } from '../App';

function Service() {
  const theme = useContext(ColorContext);
  return (
    <>
      <section>
        <MyTitle />
        <motion.div
          className='summary'
          initial='out'
          animate='in'
          exit='out'
          variants={animationOne}
          transition={transition}
        >
          <h2>שירות לאומי</h2>
          <p>
            עשיתי שירות לאומי ברשות הלאומית לבטיחות בדרכים, סוג של חמ"ל למרכז
            שומרי דרך. פזורים בארץ המון אזרחים שיש להם אפליקצייה שמתעדת עברות
            מסכנות חיים על הכביש. והחמ"ל מרכז לתוכו את כל המידע, את כל מי
            שמתקשר, את כל התמונות והסרטונים, את כל הקשר עם המתנדבים. התפקיד של
            הבני שירות, להיות בקשר עם המתנדבים, לנתח את המידע, סוג של סייבר
            מבצעי כזה.
          </p>

          <ul className='backToHome'>
            <Link to='/'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'></span>
                <span className='subject'>חזרה לדף הבית</span>
                <span className='icon'>
                  <i className='fas fa-home'></i>
                </span>
              </li>
            </Link>
          </ul>
        </motion.div>
      </section>
    </>
  );
}

export default Service;
