import React, { useContext } from 'react';
import { motion } from 'framer-motion';
import { animationOne, transition } from '../animations';
import { Link } from 'react-router-dom';
import MyTitle from './MyTitle';
import './projects.css';
import { ColorContext, headerColor } from '../App';

function Projects() {
  const theme = useContext(ColorContext);
  return (
    <>
      <section>
        <MyTitle />
        <motion.div
          className='summary'
          initial='out'
          animate='in'
          exit='out'
          variants={animationOne}
          transition={transition}
        >
          <h2>פרוייקטים</h2>
          <main className='main-cards'>
            <div className='cards-wrapper'>
              <div
                className='card'
                style={{
                  backgroundImage: 'url(/images/order-app-image.png)',
                }}
              >
                <a
                  href='https://yehudabayana.github.io/my-order/#/'
                  target='_blank'
                >
                  <h2>אפליקציית הזמנות</h2>
                </a>
              </div>
              <div
                className='card'
                style={{
                  backgroundImage: 'url(/images/challenge-app-image.png)',
                }}
              >
                <a
                  href='https://yehudabayana.github.io/challenge-web/'
                  target='_blank'
                >
                  <h2>בלי משמעות</h2>
                </a>
              </div>
              <div className='card'>
                <h2>project name2</h2>
              </div>
            </div>
          </main>

          <ul className='backToHome'>
            <Link to='/'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'></span>
                <span className='subject'>חזרה לדף הבית</span>
                <span className='icon'>
                  <i className='fas fa-home'></i>
                </span>
              </li>
            </Link>
          </ul>
        </motion.div>
      </section>
    </>
  );
}

export default Projects;
