import { Link } from 'react-router-dom';
import MyTitle from './MyTitle';
import { motion } from 'framer-motion';
import { animationOne, transition } from '../animations';
import { headerColor, ColorContext } from '../App';
import { useContext } from 'react';

function ContentSection() {
  const theme = useContext(ColorContext);
  return (
    <>
      <section>
        <MyTitle />
        <motion.div
          initial='out'
          animate='in'
          exit='out'
          variants={animationOne}
          transition={transition}
        >
          <ul>
            <Link to='/summary'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green1 }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue1, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'>1.</span>
                <span className='subject'>תקציר.</span>
                <span className='icon'>
                  <i className='fas fa-pen'></i>
                </span>
              </li>
            </Link>
            <Link to='/projects'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green2 }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue2, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'>2.</span>
                <span className='subject'>פרויקטים</span>
                <span className='icon'>
                  <i className='fas fa-briefcase'></i>
                </span>
              </li>
            </Link>
            <Link to='/education'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green2 }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue2, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'>3.</span>
                <span className='subject'>השכלה.</span>
                <span className='icon'>
                  <i className='fas fa-graduation-cap'></i>
                </span>
              </li>
            </Link>
            <Link to='/volunteer'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green3 }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue3, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'>4.</span>
                <span className='subject'>התנדבויות.</span>
                <span className='icon'>
                  <i className='fas fa-hands-helping'></i>
                </span>
              </li>
            </Link>
            <Link to='/hobbies'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green4 }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue4, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'>5.</span>
                <span className='subject'>תחביבים ופעילויות</span>
                <span className='icon'>
                  <i className='fas fa-snowboarding'></i>
                </span>
              </li>
            </Link>
            <Link to='/service'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green5 }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue5, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'>6.</span>
                <span className='subject'>שירות לאומי</span>
                <span className='icon'>
                  <i className='far fa-building'></i>
                </span>
              </li>
            </Link>
          </ul>
        </motion.div>
      </section>
    </>
  );
}

export default ContentSection;
