import React, { useContext } from 'react';
import { motion } from 'framer-motion';
import { animationOne, transition } from '../animations';
import { Link } from 'react-router-dom';
import MyTitle from './MyTitle';
import { ColorContext, headerColor } from '../App';

function Education() {
  const theme = useContext(ColorContext);
  return (
    <>
      <section>
        <MyTitle />
        <motion.div
          className='summary'
          initial='out'
          animate='in'
          exit='out'
          variants={animationOne}
          transition={transition}
        >
          <h2>השכלה</h2>
          <p>בגרות מלאה 27 יחידות עם ממוצע של 74</p>
          <p>עשיתי קורס פיתוח אפליקציות web בטק קריירה</p>
          <h2>שפות התכנות שאני יודע הן:</h2>
          <ul className='none-styled'>
            <li className='none-styled-li'>HTML</li>
            <li className='none-styled-li'>CSS</li>
            <li className='none-styled-li'>JAVASCRIPT</li>
            <li className='none-styled-li'>TYPESCRIPT</li>
            <li className='none-styled-li'>REACT</li>
            <li className='none-styled-li'>NODE-JS</li>
            <li className='none-styled-li'>C#</li>
          </ul>
          <ul className='backToHome'>
            <Link to='/'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'></span>
                <span className='subject'>חזרה לדף הבית</span>
                <span className='icon'>
                  <i className='fas fa-home'></i>
                </span>
              </li>
            </Link>
          </ul>
        </motion.div>
      </section>
    </>
  );
}

export default Education;
