import React, { useContext } from 'react';
import { motion } from 'framer-motion';
import { animationOne, transition } from '../animations';
import { Link } from 'react-router-dom';
import MyTitle from './MyTitle';
import { ColorContext, headerColor } from '../App';

function Volunteer() {
  const theme = useContext(ColorContext);
  return (
    <>
      <section>
        <MyTitle />
        <motion.div
          className='summary'
          initial='out'
          animate='in'
          exit='out'
          variants={animationOne}
          transition={transition}
        >
          <h2>התנדבויות</h2>
          <p>
            במכינה הקדם צבאית התנדבתי שעתיים בשבוע במשפחתון לילדים יתומים, ובעצם
            אני וחברי למכינה, היינו עוזרים להם בשיעורי בית, עושים להם פעילויות
            ומשחקים איתם.
          </p>
          <br />
          <p>
            כשהייתי בתיכון הייתי מתנדב פעם בשבוע בעמותת יד לחבר בחולון, <br />{' '}
            העמותה הזאת מחלקת אוכל למשפחות נזקקות, חלקי בהתנדבות היה לארוז
            קרטונים של אוכל.
          </p>

          <ul className='backToHome'>
            <Link to='/'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'></span>
                <span className='subject'>חזרה לדף הבית</span>
                <span className='icon'>
                  <i className='fas fa-home'></i>
                </span>
              </li>
            </Link>
          </ul>
        </motion.div>
      </section>
    </>
  );
}

export default Volunteer;
