import MyTitle from './MyTitle';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { animationOne, transition } from '../animations';
import { ColorContext, headerColor } from '../App';
import { useContext } from 'react';
function Summary() {
  const theme = useContext(ColorContext);
  return (
    <>
      <section>
        <MyTitle />
        <motion.div
          className='summary'
          initial='out'
          animate='in'
          exit='out'
          variants={animationOne}
          transition={transition}
        >
          <h2>תקציר</h2>
          <p>
            קוראים לי יהודה ביינה, אני גר בבית שמש נולדתי בשנת 2000, סיימתי את
            לימודי בהצלחה במקווה ישראל חולון, לאחר לימודי במקווה ישראל נרשמתי
            למכינה קדם צבאים בירושליםת בין לבין עד שהתחלנו את המכינה היו לי
            חודשיים פנויים אז עבדתי באיקאה חנות הרהיטים. בזמן המכינה הצבא הודיע
            לי שאני מקבל פטור מהצבא בשל בעיות רפואיות ניסיתי איכשהו לערער על
            הפטור עד שהבנתי שהצבא לא לוקח ריזיקות. בעקבות כך לאחר שסיימתי את
            המכינה, עשיתי שירות לאומי ברשות הלאומית לבטיחות בדרכים, סוג של חמ"ל
            למרכז שומרי דרך. פזורים בארץ המון אזרחים שיש להם אפליקצייה שמתעדת
            עברות מסכנות חיים על הכביש. והחמ"ל מרכז לתוכו את כל המידע, את כל מי
            שמתקשר, את כל התמונות והסרטונים, את כל הקשר עם המתנדבים. התפקיד של
            הבני שירות, להיות בקשר עם המתנדבים, לנתח את המידע, סוג של סייבר
            מבצעי כזה. לאחר השירות לאומי נכנסתי לקורס full stack development בטק
            קריירה וכיום סיימתי את הקורס בהצלחה ומחפש את מקומי בתעשייה.
          </p>

          <ul className='backToHome'>
            <Link to='/'>
              <li
                style={
                  theme === 'green'
                    ? { backgroundColor: headerColor.green }
                    : theme === 'blue'
                    ? { backgroundColor: headerColor.blue, color: 'white' }
                    : { backgroundColor: headerColor.red, color: 'white' }
                }
              >
                <span className='number'></span>
                <span className='subject'>חזרה לדף הבית</span>
                <span className='icon'>
                  <i className='fas fa-home'></i>
                </span>
              </li>
            </Link>
          </ul>
        </motion.div>
      </section>
    </>
  );
}

export default Summary;
