import './App.css';
import { Route, Switch, useLocation, Redirect } from 'react-router-dom';
import ImageSection from './components/ImageSection';
import ContentSection from './components/ContentSection';
import Summary from './components/Summary';
import Projects from './components/Projects';
import Education from './components/Education';
import Volunteer from './components/Volunteer';
import Hobbies from './components/Hobbies';
import Service from './components/Service';
import { AnimatePresence } from 'framer-motion';
import GlobalStyle from './GlobalStyle';
import { useState, createContext } from 'react';

export const headerColor = {
  green: 'rgb(137, 190, 0)',
  green1: 'rgb(154, 206, 52)',
  green2: 'rgba(155, 202, 60, 0.795)',
  green3: 'rgba(161, 202, 78, 0.733)',
  green4: 'rgba(174, 212, 96, 0.712)',
  green5: 'rgba(194, 221, 136, 0.685)',
  green6: 'rgba(204, 226, 159, 0.541)',
  blue: 'rgb(66, 0, 190)',
  blue1: 'rgb(52, 75, 206)',
  blue2: 'rgba(60, 74, 202, 0.795)',
  blue3: 'rgba(78, 90, 202, 0.733)',
  blue4: 'rgba(96, 104, 212, 0.712)',
  blue5: 'rgba(136, 149, 221, 0.685)',
  blue6: 'rgba(160, 159, 226, 0.541)',
  red: 'rgb(255 0 0)',
};

export const ColorContext = createContext();
const ContextProvider = ColorContext.Provider;

function App() {
  const [theme, setTheme] = useState('green');
  let location = useLocation();

  function toRedColor() {
    setTheme('red');
  }
  function toBlueColor() {
    setTheme('blue');
  }
  function toGreenColor() {
    setTheme('green');
  }
  return (
    <div className='App'>
      <ContextProvider value={theme}>
        <header
          style={
            theme === 'green'
              ? { backgroundColor: headerColor.green }
              : theme === 'blue'
              ? { backgroundColor: headerColor.blue }
              : { backgroundColor: headerColor.red }
          }
        >
          <h1>יהודה ביינה</h1>
          <div className='dropdown'>
            <button
              className='dropbtn'
              style={
                theme === 'green'
                  ? { backgroundColor: headerColor.green }
                  : theme === 'blue'
                  ? { backgroundColor: headerColor.blue }
                  : { backgroundColor: headerColor.red }
              }
            >
              choose background color{' '}
            </button>
            <div className='dropdown-content'>
              <a href='#' onClick={toRedColor}>
                red
              </a>
              <a href='#' onClick={toBlueColor}>
                blue
              </a>
              <a href='#' onClick={toGreenColor}>
                green (recomended)
              </a>
            </div>
          </div>
        </header>
        <main>
          <GlobalStyle />
          <AnimatePresence exitBeforeEnter>
            <Switch location={location} key={location.pathname}>
              <Route path='/' exact>
                <ContentSection />
              </Route>
              <Route path='/summary' component={Summary} />
              <Route path='/projects' component={Projects} />
              <Route path='/education' component={Education} />
              <Route path='/volunteer' component={Volunteer} />
              <Route path='/hobbies' component={Hobbies} />
              <Route path='/service' component={Service} />
              <Redirect to='/' />
            </Switch>
          </AnimatePresence>
          <ImageSection />
        </main>
        <footer
          style={
            theme === 'green'
              ? { backgroundColor: headerColor.green }
              : theme === 'blue'
              ? { backgroundColor: headerColor.blue }
              : { backgroundColor: headerColor.red }
          }
        >
          <p>all right recived &copy; 2021 Yuda Bayana</p>
        </footer>
      </ContextProvider>
    </div>
  );
}

export default App;
